"use client"

import { useEffect, useState } from "react"
import { useParams } from "next/navigation"
import { Header } from "@/components/layout/header"
import { Footer } from "@/components/layout/footer"
import { SaleTimer } from "@/components/home/sale-timer"
import { ProductDetails } from "@/components/product/product-details"
import { ProductCarousel } from "@/components/home/product-carousel"
import { publicProductsAPI, Product } from "@/lib/api"

export default function ProductPage() {
  const params = useParams()
  const productId = params?.id ? parseInt(params.id as string) : null
  const [product, setProduct] = useState<Product | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    if (productId) {
      fetchProduct()
    }
  }, [productId])

  const fetchProduct = async () => {
    if (!productId) return
    try {
      const data = await publicProductsAPI.getById(productId)
      setProduct(data)
    } catch (error) {
      console.error("Failed to fetch product:", error)
    } finally {
      setIsLoading(false)
    }
  }

  if (isLoading) {
    return (
      <div className="min-h-screen">
        <SaleTimer />
        <Header />
        <main className="flex items-center justify-center min-h-[60vh]">
          <p className="text-[#2E5E99]">Loading product...</p>
        </main>
        <Footer />
      </div>
    )
  }

  if (!product) {
    return (
      <div className="min-h-screen">
        <SaleTimer />
        <Header />
        <main className="flex items-center justify-center min-h-[60vh]">
          <p className="text-[#2E5E99]">Product not found</p>
        </main>
        <Footer />
      </div>
    )
  }

  return (
    <div className="min-h-screen">
      <SaleTimer />
      <Header />
      <main>
        <ProductDetails product={product} />
        <section className="border-t border-border">
          <ProductCarousel />
        </section>
      </main>
      <Footer />
    </div>
  )
}
