"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Image from "next/image"
import { Heart, Share2, Minus, Plus, Check } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Product } from "@/lib/api"

interface ProductDetailsProps {
  product: Product
}

export function ProductDetails({ product }: ProductDetailsProps) {
  const router = useRouter()
  const [selectedImage, setSelectedImage] = useState(0)
  const [selectedSize, setSelectedSize] = useState("")
  const [quantity, setQuantity] = useState(1)

  // Use gallery images if available, otherwise cover image, otherwise placeholder
  const productImages = product.gallery_images && product.gallery_images.length > 0
    ? [product.image_url || "/placeholder.svg", ...product.gallery_images]
    : product.image_url
      ? [product.image_url]
      : ["/placeholder.svg"]

  // Use product sizes if available, otherwise default
  const sizes = product.sizes && product.sizes.length > 0
    ? product.sizes
    : ["XS", "S", "M", "L", "XL"]

  // Use product colors if available, otherwise default
  const colors = product.colors && product.colors.length > 0
    ? product.colors
    : [
      { name: "Beige", value: "#d4c4b0" },
      { name: "Navy", value: "#0D2440" },
      { name: "Sapphire", value: "#2E5E99" },
    ]
  const [selectedColor, setSelectedColor] = useState(colors[0])

  const handleAddToCart = () => {
    // Add to cart logic (can be implemented with localStorage or context)
    const cartItem = {
      id: product.id,
      name: product.name,
      price: product.price,
      quantity,
      size: selectedSize,
      color: selectedColor.name,
      image: product.image_url || "/placeholder.svg",
    }

    // Get existing cart from localStorage
    const existingCart = typeof window !== "undefined" ? localStorage.getItem("cart") : null
    const cart = existingCart ? JSON.parse(existingCart) : []

    // Check if item already exists in cart
    const existingItemIndex = cart.findIndex(
      (item: any) => item.id === product.id && item.size === selectedSize && item.color === selectedColor.name
    )

    if (existingItemIndex >= 0) {
      cart[existingItemIndex].quantity += quantity
    } else {
      cart.push(cartItem)
    }

    if (typeof window !== "undefined") {
      localStorage.setItem("cart", JSON.stringify(cart))
    }

    router.push("/cart")
  }

  return (
    <section className="py-6 sm:py-8 lg:py-12 bg-[#E7F0FA]">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 sm:gap-8 lg:gap-12">
          {/* Images */}
          <div className="space-y-3 sm:space-y-4">
            <div className="relative aspect-[3/4] overflow-hidden rounded-lg sm:rounded-xl bg-white">
              <Image
                src={productImages[selectedImage] || "/placeholder.svg"}
                alt={product.name}
                fill
                className="object-cover"
              />
            </div>
            {productImages.length > 1 && (
              <div className="grid grid-cols-4 gap-2 sm:gap-4">
                {productImages.map((image, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedImage(index)}
                    className={`relative aspect-square overflow-hidden rounded-lg bg-white ${selectedImage === index ? "ring-2 ring-[#2E5E99]" : "ring-1 ring-[#7BA4D0]/30"
                      }`}
                  >
                    <Image
                      src={image || "/placeholder.svg"}
                      alt={`${product.name} ${index + 1}`}
                      fill
                      className="object-cover"
                    />
                  </button>
                ))}
              </div>
            )}
          </div>

          {/* Details */}
          <div className="lg:py-4">
            <div className="flex items-start justify-between gap-4 mb-4 sm:mb-6">
              <div>
                <h1 className="text-2xl sm:text-3xl lg:text-4xl font-bold mb-2 text-[#0D2440]">{product.name}</h1>
                <div className="flex items-center gap-2 sm:gap-3" style={{ fontFamily: "var(--font-body)" }}>
                  <span className="text-xl sm:text-2xl font-semibold text-[#2E5E99]">${product.price.toFixed(2)}</span>
                </div>
                {product.stock === 0 && (
                  <p className="text-sm text-red-600 mt-2">Out of Stock</p>
                )}
                {product.stock > 0 && product.stock < 20 && (
                  <p className="text-sm text-yellow-600 mt-2">Only {product.stock} left in stock</p>
                )}
              </div>
              <div className="flex gap-2">
                <Button
                  variant="outline"
                  size="icon"
                  className="h-9 w-9 sm:h-10 sm:w-10 border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 bg-transparent"
                >
                  <Heart className="h-4 w-4 sm:h-5 sm:w-5" />
                </Button>
                <Button
                  variant="outline"
                  size="icon"
                  className="h-9 w-9 sm:h-10 sm:w-10 border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 bg-transparent"
                >
                  <Share2 className="h-4 w-4 sm:h-5 sm:w-5" />
                </Button>
              </div>
            </div>

            {product.description && (
              <p
                className="text-sm sm:text-base text-[#0D2440]/80 mb-6 sm:mb-8"
                style={{ fontFamily: "var(--font-body)" }}
              >
                {product.description}
              </p>
            )}

            {/* Color Selection */}
            <div className="mb-5 sm:mb-6">
              <p className="text-sm font-medium mb-2 sm:mb-3 text-[#0D2440]" style={{ fontFamily: "var(--font-body)" }}>
                Color: <span className="text-[#2E5E99]">{selectedColor.name}</span>
              </p>
              <div className="flex gap-2 sm:gap-3">
                {colors.map((color) => (
                  <button
                    key={color.name}
                    onClick={() => setSelectedColor(color)}
                    className={`w-9 h-9 sm:w-10 sm:h-10 rounded-full relative ${selectedColor.name === color.name ? "ring-2 ring-[#2E5E99] ring-offset-2" : ""
                      }`}
                    style={{ backgroundColor: color.value }}
                  >
                    {selectedColor.name === color.name && (
                      <Check className="absolute inset-0 m-auto h-4 w-4 sm:h-5 sm:w-5 text-white drop-shadow-md" />
                    )}
                  </button>
                ))}
              </div>
            </div>

            {/* Size Selection */}
            <div className="mb-5 sm:mb-6">
              <div className="flex items-center justify-between mb-2 sm:mb-3">
                <p className="text-sm font-medium text-[#0D2440]" style={{ fontFamily: "var(--font-body)" }}>
                  Size
                </p>
                <button
                  className="text-xs sm:text-sm text-[#2E5E99] underline"
                  style={{ fontFamily: "var(--font-body)" }}
                >
                  Size Guide
                </button>
              </div>
              <div className="flex flex-wrap gap-2 sm:gap-3">
                {sizes.map((size) => (
                  <button
                    key={size}
                    onClick={() => setSelectedSize(size)}
                    className={`w-12 sm:w-14 h-10 sm:h-12 border rounded-lg text-xs sm:text-sm font-medium transition-colors ${selectedSize === size
                        ? "border-[#2E5E99] bg-[#2E5E99] text-[#E7F0FA]"
                        : "border-[#7BA4D0] text-[#0D2440] hover:border-[#2E5E99]"
                      }`}
                    style={{ fontFamily: "var(--font-body)" }}
                  >
                    {size}
                  </button>
                ))}
              </div>
            </div>

            {/* Quantity */}
            <div className="mb-6 sm:mb-8">
              <p className="text-sm font-medium mb-2 sm:mb-3 text-[#0D2440]" style={{ fontFamily: "var(--font-body)" }}>
                Quantity
              </p>
              <div className="flex items-center border border-[#7BA4D0] rounded-lg w-fit">
                <button
                  onClick={() => setQuantity(Math.max(1, quantity - 1))}
                  className="p-2.5 sm:p-3 hover:bg-[#7BA4D0]/20 transition-colors text-[#2E5E99]"
                >
                  <Minus className="h-4 w-4" />
                </button>
                <span
                  className="w-10 sm:w-12 text-center font-medium text-[#0D2440]"
                  style={{ fontFamily: "var(--font-body)" }}
                >
                  {quantity}
                </span>
                <button
                  onClick={() => setQuantity(quantity + 1)}
                  className="p-2.5 sm:p-3 hover:bg-[#7BA4D0]/20 transition-colors text-[#2E5E99]"
                >
                  <Plus className="h-4 w-4" />
                </button>
              </div>
            </div>

            {/* Add to Cart */}
            <div className="flex flex-col sm:flex-row gap-3 sm:gap-4 mb-6 sm:mb-8">
              <Button
                size="lg"
                onClick={handleAddToCart}
                disabled={product.stock === 0 || !selectedSize}
                className="flex-1 uppercase tracking-wider bg-[#2E5E99] hover:bg-[#0D2440] text-[#E7F0FA] h-12 disabled:opacity-50"
              >
                {product.stock === 0 ? "Out of Stock" : !selectedSize ? "Select Size" : "Add to Cart"}
              </Button>
              <Button
                variant="outline"
                size="lg"
                disabled={product.stock === 0 || !selectedSize}
                className="flex-1 uppercase tracking-wider bg-transparent border-[#2E5E99] text-[#2E5E99] hover:bg-[#2E5E99] hover:text-[#E7F0FA] h-12 disabled:opacity-50"
              >
                Buy Now
              </Button>
            </div>

            {/* Product Details */}
            <div className="border-t border-[#7BA4D0]/30 pt-6 sm:pt-8">
              <h3 className="text-sm font-semibold uppercase tracking-wider mb-3 sm:mb-4 text-[#0D2440]">
                Product Details
              </h3>
              <ul className="space-y-2" style={{ fontFamily: "var(--font-body)" }}>
                <li className="text-xs sm:text-sm text-[#0D2440]/80 flex items-center gap-2">
                  <span className="w-1.5 h-1.5 rounded-full bg-[#2E5E99]" />
                  Category: {product.category}
                </li>
                <li className="text-xs sm:text-sm text-[#0D2440]/80 flex items-center gap-2">
                  <span className="w-1.5 h-1.5 rounded-full bg-[#2E5E99]" />
                  Stock: {product.stock} available
                </li>
                <li className="text-xs sm:text-sm text-[#0D2440]/80 flex items-center gap-2">
                  <span className="w-1.5 h-1.5 rounded-full bg-[#2E5E99]" />
                  Status: {product.status}
                </li>
                {product.description && (
                  <li className="text-xs sm:text-sm text-[#0D2440]/80 flex items-center gap-2">
                    <span className="w-1.5 h-1.5 rounded-full bg-[#2E5E99]" />
                    {product.description}
                  </li>
                )}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
