"use client"

import { useRef, useState, useEffect } from "react"
import Image from "next/image"
import Link from "next/link"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"

const products = [
  { id: 1, name: "Linen Blazer", price: 299, image: "/beige-linen-blazer-fashion.jpg", category: "Outerwear" },
  { id: 2, name: "Silk Blouse", price: 189, image: "/cream-silk-blouse-elegant.jpg", category: "Tops" },
  { id: 3, name: "Wide Leg Trousers", price: 159, image: "/tan-wide-leg-trousers-fashion.jpg", category: "Bottoms" },
  { id: 4, name: "Cashmere Sweater", price: 349, image: "/camel-cashmere-sweater-luxury.jpg", category: "Knitwear" },
  { id: 5, name: "Leather Handbag", price: 450, image: "/brown-leather-handbag-minimal.jpg", category: "Accessories" },
  { id: 6, name: "Cotton Dress", price: 229, image: "/cream-cotton-midi-dress-elegant.jpg", category: "Dresses" },
  { id: 7, name: "Wool Coat", price: 599, image: "/camel-wool-coat-classic.jpg", category: "Outerwear" },
  { id: 8, name: "Satin Skirt", price: 149, image: "/champagne-satin-midi-skirt.jpg", category: "Bottoms" },
]

export function ProductCarousel() {
  const scrollRef = useRef<HTMLDivElement>(null)
  const [canScrollLeft, setCanScrollLeft] = useState(false)
  const [canScrollRight, setCanScrollRight] = useState(true)

  const checkScroll = () => {
    if (scrollRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = scrollRef.current
      setCanScrollLeft(scrollLeft > 0)
      setCanScrollRight(scrollLeft < scrollWidth - clientWidth - 10)
    }
  }

  useEffect(() => {
    checkScroll()
    window.addEventListener("resize", checkScroll)
    return () => window.removeEventListener("resize", checkScroll)
  }, [])

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = 280
      scrollRef.current.scrollBy({
        left: direction === "left" ? -scrollAmount : scrollAmount,
        behavior: "smooth",
      })
      setTimeout(checkScroll, 300)
    }
  }

  return (
    <section className="py-12 sm:py-16 lg:py-20">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="flex flex-col sm:flex-row sm:items-end justify-between gap-4 mb-8 sm:mb-10">
          <div>
            <p
              className="text-xs sm:text-sm uppercase tracking-[0.2em] sm:tracking-[0.3em] text-[#2E5E99] mb-2"
              style={{ fontFamily: "var(--font-body)" }}
            >
              Curated For You
            </p>
            <h2 className="text-2xl sm:text-3xl lg:text-4xl font-bold text-[#0D2440]">Featured Products</h2>
          </div>
          <div className="flex gap-2">
            <Button
              variant="outline"
              size="icon"
              onClick={() => scroll("left")}
              disabled={!canScrollLeft}
              className="rounded-full h-10 w-10 sm:h-11 sm:w-11 border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 disabled:opacity-40"
            >
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Button
              variant="outline"
              size="icon"
              onClick={() => scroll("right")}
              disabled={!canScrollRight}
              className="rounded-full h-10 w-10 sm:h-11 sm:w-11 border-[#7BA4D0] text-[#2E5E99] hover:bg-[#7BA4D0]/20 disabled:opacity-40"
            >
              <ChevronRight className="h-5 w-5" />
            </Button>
          </div>
        </div>

        <div
          ref={scrollRef}
          onScroll={checkScroll}
          className="flex gap-4 sm:gap-6 overflow-x-auto scrollbar-hide pb-4 -mx-4 px-4 snap-x snap-mandatory"
          style={{ scrollbarWidth: "none", msOverflowStyle: "none" }}
        >
          {products.map((product) => (
            <Link
              key={product.id}
              href={`/product/${product.id}`}
              className="group flex-shrink-0 w-[200px] sm:w-[250px] lg:w-[280px] snap-start"
            >
              <div className="relative aspect-[3/4] overflow-hidden rounded-lg bg-[#E7F0FA] mb-3 sm:mb-4">
                <Image
                  src={product.image || "/placeholder.svg"}
                  alt={product.name}
                  fill
                  className="object-cover transition-transform duration-500 group-hover:scale-105"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-[#0D2440]/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity" />
              </div>
              <p
                className="text-[10px] sm:text-xs uppercase tracking-wider text-[#2E5E99] mb-1"
                style={{ fontFamily: "var(--font-body)" }}
              >
                {product.category}
              </p>
              <h3 className="font-medium text-base sm:text-lg mb-1 text-[#0D2440]">{product.name}</h3>
              <p className="text-[#0D2440]/80 text-sm sm:text-base" style={{ fontFamily: "var(--font-body)" }}>
                ${product.price}
              </p>
            </Link>
          ))}
        </div>
      </div>
    </section>
  )
}
